$global-font-icon: 'social';
@font-face {
  font-family: $global-font-icon;
  src:url("../assets/fonts/myicons.eot");
  src:url("../assets/fonts/myicons.?#iefix") format("embedded-opentype"),
    url("../assets/fonts/myicons.woff") format("woff"),
    url("../assets/fonts/myicons.ttf") format("truetype"),
    url("../assets/fonts/myicons.#myicons") format("svg");
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: $global-font-icon;

  &:before {
    /* Some common icon stuff */
  }

  &.icon--arrow-down {
    &:before {
      content: $icon-arrow-down;
    }
  }
  &.icon--dark-e-mail {
    &:before {
      content: $icon-dark-e-mail;
    }
  }
  &.icon--dark-padlock {
    &:before {
      content: $icon-dark-padlock;
    }
  }
  &.icon--e-mail-envelope {
    &:before {
      content: $icon-e-mail-envelope;
    }
  }
  &.icon--fb {
    &:before {
      content: $icon-fb;
    }
  }
  &.icon--open {
    &:before {
      content: $icon-open;
    }
  }
  &.icon--padlock {
    &:before {
      content: $icon-padlock;
    }
  }
  &.icon--right {
    &:before {
      content: $icon-right;
    }
  }
  &.icon--tw {
    &:before {
      content: $icon-tw;
    }
  }

}